






























































































.container {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  background: #f5f5f5;
  color: #666;
  .section {
    margin-bottom: 20px;
    font-size: 28px;
    padding: 30px;
    padding-right: 0;
    background: #fff;
    .section-title {
      padding-bottom: 20px;
      border-bottom: 1px solid #f0f0f0;
      font-size: 32px;
      line-height: 1em;
    }
    .section-content {
      color: #999;
      font-size: 28px;
      line-height: 1.8em;
      padding-top: 20px;
      padding-right: 30px;
    }
  }
}

.movie-box {
  padding-bottom: 20px;
  padding-right: 30px;
  border-bottom: 1px solid #f0f0f0;
  line-height: 1.5em;
  font-size: 30px;
}

.movie-name {
  color: #333;
  font-size: 36px;
}

.watch-time {
  color: #f03d37;
}

.price-box {
  display: flex;
  justify-content: space-between;
  padding-right: 30px;
  padding-top: 30px;
  .price-num {
    color: #f03d37;
    font-size: 30px;
    .yuan {
      font-size: 20px;
    }
  }
}

.server {
  font-size: 26px;
  color: #999;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  border-top: 1px solid #f0f0f0;
  .price-num {
    color: #f03d37;
    font-size: 36px;
  }
  .yuan {
    font-size: 40px;
  }
  .payment {
    padding: 20px 30px;
    text-align: right;
    font-size: 32px;
  }
  .payment-btn {
    height: 2.5em;
    line-height: 2.5em;
    background: #f90;
    margin: 0 30px 20px;
    color: #fff;
    text-align: center;
    font-size: 34px;
    border-radius: 8px;
  }
}

.seats-block {
  display: flex;
  .item {
    padding-right: 10px;
    box-sizing: border-box;
  }
}
